import React, { useState } from 'react';
import './Setting.css';
import { FiSettings } from "react-icons/fi";
import { MdSecurity } from "react-icons/md";
import { TiInfoLarge } from "react-icons/ti";
import { Link, Route, Switch, useLocation } from 'react-router-dom';
import { Descriptions, Collapse } from 'antd';


let ustkInfor =  localStorage.getItem('ustk') ? JSON.parse(localStorage.getItem('ustk')).info : null ;

const { Panel } = Collapse;

const General = () => {
  const [styleInputAddContact, setStyleInputAddContact] = useState();

  const openInputAddContact = () => {
    setStyleInputAddContact({
      display: 'block',
      transition: 'all 0.2s ease',
    })
  }

  return (
    <div className="general-container">
      <div className="general-title">
        <h5> Ci t ti khon chung</h5>
      </div>
      <div className="general-content">
        <Collapse defaultActiveKey={['0']}>
          <Panel header={<SettingGeneralName />} key="1">
            <div className="setting-general-name">
              <b>Tn ca bn s thay i trn Mohi</b>
              <form>
                <div className="setting-form-edit-name">
                  <label htmlFor="">H</label>
                  <input type="text" defaultValue={ustkInfor.user_first_name }  />
                </div>
                <div className="setting-form-edit-name">
                  <label htmlFor="">Tn</label>
                  <input type="text" defaultValue={ustkInfor.user_last_name } />
                </div>
                <div className="setting-form-edit-name-button">
                  <button type="submit">Xc nhn thay i</button>
                  <button type="submit">Hy</button>
                </div>

              </form>
            </div>

          </Panel>
          <Panel header={<SettingGeneralNameUser />} key="2">
            <div className="setting-general-name">
              <b>Tn ngi dng cng khai ca bn ging vi tn trong a ch dng thi gian ca bn</b>
              <form>
                <div className="setting-form-edit-name">
                  <label htmlFor="">Tn ngi dng</label>
                  <input type="text" defaultValue={ustkInfor.user_username}  />
                </div>
                <div className="setting-form-edit-name-button">
                  <button type="submit">Xc nhn thay i</button>
                  <button type="submit">Hy</button>
                </div>

              </form>
            </div>
          </Panel>
        </Collapse>
      </div>
    </div>
  )
}
const SettingGeneralName = () => {
  return (
    <>
      <Descriptions className="general-information">
        <Descriptions.Item className="general-item">Tn</Descriptions.Item>
        <Descriptions.Item >
        <p>{ustkInfor.user_first_name + ' ' + ustkInfor.user_last_name}</p>
        </Descriptions.Item>
        <Descriptions.Item className="setting-general-button">
          <button type="submit" type="info">Chnh sa</button>
        </Descriptions.Item>
        <SettingGeneralName />
      </Descriptions>
    </>
  );
}
const SettingGeneralNameUser = () => {
  return (
    <Descriptions className="general-information">
      <Descriptions.Item className="general-item">Tn ngi dng</Descriptions.Item>
      <Descriptions.Item>
        https://www.mohi.com/{ustkInfor.user_username}
          </Descriptions.Item>
      <Descriptions.Item className="setting-general-button">
        <button type="submit">Chnh sa</button>
      </Descriptions.Item>
    </Descriptions>
  );
}
const SettingGeneralContact = () => {
  return (
    <Descriptions className="general-information">
      <Descriptions.Item className="general-item">Lin h</Descriptions.Item>
      <Descriptions.Item>
        Email : {ustkInfor.user_email}
          </Descriptions.Item>
      <Descriptions.Item className="setting-general-button">
        <button type="submit">Chnh sa</button>
      </Descriptions.Item>
    </Descriptions>
  );
}
const Password = () => {

  const [openPopupChangePassword, setOpenPopupChangePassword] = useState(false);

  const openPopupChangePasswordFunc = () => {
    setOpenPopupChangePassword(!openPopupChangePassword)
  }

  return (
    <>
      <div className="general-container">
        <div className="general-title">
          <h5> i mt khu</h5>
        </div>
        {!openPopupChangePassword ?
        <div className="setting-password">
          <h6>	Bn nn s dng mt khu mnh m mnh cha s dng  u khc</h6>
          <form >
            <div className="setting-form-edit-password">
              <label htmlFor="">Mt khu hin ti</label>
              <input type="password" />
            </div>
            <div className="setting-form-edit-password">
              <label htmlFor="">Mt khu mi</label>
              <input type="password" />
            </div>
            <div className="setting-form-edit-password">
              <label htmlFor="">Nhp li mt khu mi</label>
              <input type="password" />
            </div>
            <div className="setting-form-edit-password">
              <div className="setting-general-add-email-click" onClick={openPopupChangePasswordFunc}>Qun mt khu?</div>
            </div>
            <div className="setting-form-edit-name-button">
              <button type="submit">Xc nhn thay i</button>
              <button type="submit">Hy</button>
            </div>
          </form>
        </div>
        :
        <div className="setting-password">
          <form>
          <div className="setting-form-change-password">
            <label htmlFor="">Nhp email hoc s in thoi  ly li mt khu</label>
            <input type="text" placeholder="Nhp email hoc s in thoi..." />
          </div>
          <div className="setting-form-change-password-button">
              <button type="submit">Gi</button>
              <button onClick={openPopupChangePasswordFunc}>Quay li</button>
          </div>
        </form>
      </div>
      }
      </div>
    </>
  )
}
const Contact = () => {
  return (
    <>
      <div className="general-container">
        <div className="general-title">
          <h5> Thng tin ca bn trn Mohi</h5>
        </div>
        <div className="setting-info">
          <p>	Bn nn s dng mt khu mnh m mnh cha s dng  u khc</p>

        </div>
      </div>
    </>
  )
}
function Setting() {
  const path = useLocation().pathname.split('/');
  const isActive = path[2];
  return (
      <div className="setting-container">
        <div className="setting-content">
          <div className="setting-content-left">
            <ul>
              <Link to="/setting/general">
                <li className={isActive === 'general' || !isActive ? 'active' : ''}>
                  <span><FiSettings /> Ci t chung</span>
                </li>
              </Link>
              <Link to="/setting/password">
                <li className={isActive === 'password' ? 'active' : ''}>
                  <span><MdSecurity /> i mt khu</span>
                </li>
              </Link>
            </ul>
          </div>
          <div className="setting-content-right">
            <Switch>
              <Route path="/setting/general" component={General} />
              <Route path="/setting/password" component={Password} />
              <Route path="/setting/contact" component={Contact} />
              <Route component={General} />
            </Switch>
          </div>
        </div>
      </div>
  )
}
export default Setting;
