import React from 'react';
import './SettingMenu.css';
import { Link } from 'react-router-dom';

function MenuSetting(){

  const logoutFunc = () => {
    localStorage.removeItem('ustk');
    window.location = '/'
  }

  return(
    <ul className="setting-menu-container">
        <li className="setting-menu-items">
            <Link to="/pages">Qun l trang</Link>
        </li>
        <li className="setting-menu-items">
            <Link to="/groups">Qun l nhm</Link>
        </li>
        <li className="setting-menu-items">
            <Link to="">Nht k hot ng</Link>
        </li>
        <li className="setting-menu-items">
            <Link to="/setting">Ci t</Link>
        </li>
        <li className="setting-menu-items" onClick={logoutFunc}>
            <span >ng xut</span>
        </li>
    </ul>
)
};

export default MenuSetting;
